import React, {useState, useEffect} from "react"
import styles from "../AppStyles.module.css"
import {useMutation} from "@apollo/client"
import {SELL_PLAYER} from "../graphql/queries"

const AuctionView = (props) => {
    const endTime = props.nominatedPlayer !== null ? props.nominatedPlayer.timeLeft : 30000
    const calculateTimeLeft = () => {
        return (endTime-Date.now())/1000
    }
    const [timeLeft, setTimeLeft] = useState(calculateTimeLeft())    
    const [sellPlayer] = useMutation(SELL_PLAYER)

    useEffect( () => {
        setTimeout( () => {
            if (timeLeft>0){
                setTimeLeft(calculateTimeLeft())
                // if(props.xfinalizeSaleButton===false ){
                //     props.l√§hetys(true)
                // }
            } else {
                setTimeLeft(calculateTimeLeft())
                // if(props.xfinalizeSaleButton===true & props.nominatedPlayer!==null){
                //     props.l√§hetys(false)
                // }                          
            }
        }, 1000)
    })
    if (props.turn===null){
        return <div><h1>Starting soon</h1></div>
    }
   // console.log(props)
    const finalizeSale = async()=> {
        if(timeLeft<=1){
            const soldPlayer = {
                owner:props.validatedFirstBid.bidder, 
                playerName:props.validatedFirstBid.player.playerName, 
                nflTeam: props.validatedFirstBid.player.nflTeam,
                position: props.validatedFirstBid.player.position,
                oldId: props.validatedFirstBid.player.id,
                bye: props.validatedFirstBid.player.bye,
                price: props.validatedFirstBid.currentPrice
            }
            await sellPlayer({variables: soldPlayer})
            console.log("T√§ss√§")
            props.start(true)
            console.log("Ja t√§ss√§")
        }
    }

    //const playerBeingSold = props.nominatedPlayer
    if (props.nominatedPlayer===null){
        return(
            <div>
                <h1>{props.turn.owner} is nominating...</h1>
            </div>
        )
    }

    return (
        <div>
        <div className={styles.Timer}>
            <h2>0:{Math.trunc(timeLeft)<10 ? "0":null}{Math.trunc(timeLeft) >= 0 ? Math.trunc(timeLeft) : 0}</h2>
        </div>
        {props.manager.owner==="Erik" ? <button className={styles.mini} onClick={()=> finalizeSale()}>Nappi</button> : null}
        </div>
    )
}

export default AuctionView;