import React, {useState} from "react";
import styles from "../AppStyles.module.css"
import AuctionView from "./AuctionView";
import {useMutation} from "@apollo/client"
import {CHANGE_BID} from "../graphql/queries"
import {teamInfo, validateBid, bidButtonDisabled} from "../utils/teamUtils"

const AuctionComponent = (props) => {
    let filteredQueue = props.playerQueue.filter((value, index) => props.playerQueue.indexOf(value) === index)
    const [bid]=useMutation(CHANGE_BID)
    // const [sellPlayer] = useMutation(SELL_PLAYER)
    let validatedFirstBid={}
    if(props.nominatedPlayer){
        validatedFirstBid={...props.nominatedPlayer, currentPrice: props.nominatedPlayer.currentPrice===null ? 1 : props.nominatedPlayer.currentPrice}

    }
    // console.log(validatedFirstBid.ass === true)
    const currentBidPlusOne =  validatedFirstBid !== null ? validatedFirstBid.currentPrice+1 :  1
    
    const [customBid, setCustomBid] = useState(validatedFirstBid !== null ? validatedFirstBid.currentPrice+1 : 2) 
    // const [xfinalizeSaleButton, setxFinalizeSaleButton] = useState(true)
    const managerRestrictions = teamInfo(props.manager.players.length, props.manager.salary)
    //Lis√§ys
    //const firstBid = props.nominatedPlayer.currentPrice//=null
    
    console.log(currentBidPlusOne)
    //console.log(currentBidPlusOne)
    //const fakeBid = 
    const highestBidder = validatedFirstBid !== null ? props.teams.find(t => t.id === validatedFirstBid.bidder) : null

    const bidPlusOne = async(team) => {
        const newestBid= {bidder:team.id, playerId:validatedFirstBid.player.id, currentPrice:Number(currentBidPlusOne) }
        const curManager = props.teams.find(t => t.id === team.id)
        const curManagerRestrictions=teamInfo(curManager.players.length, curManager.salary)
        if(validateBid(validatedFirstBid, newestBid, curManagerRestrictions)){            
            bid({ variables: newestBid })
        } 
    }

    const submit = async(event) => {
        event.preventDefault()        
        const newestBid= {bidder:props.manager.id, playerId:validatedFirstBid.player.id, currentPrice:Number(customBid) }
        if(validateBid(validatedFirstBid, newestBid, managerRestrictions)){
            bid({ variables: newestBid })
        } else {
            console.log("Virheilmoitus")   //Aktivoi virhe-ilmoitus t√§ss√§
        }        
    }

    // const finalizeSale = async()=> {
        
    //     const soldPlayer = {
    //         owner:props.nominatedPlayer.bidder, 
    //         playerName:props.nominatedPlayer.player.playerName, 
    //         nflTeam: props.nominatedPlayer.player.nflTeam,
    //         position: props.nominatedPlayer.player.position,
    //         oldId: props.nominatedPlayer.player.id,
    //         bye: props.nominatedPlayer.player.bye,
    //         price: props.nominatedPlayer.currentPrice
    //     }
    //     await sellPlayer({variables: soldPlayer})
    //     props.start(true)
    // }



    return(
        <div className={props.turn === null ? styles.SelfInfoOrange : props.manager.id===props.turn.id & validatedFirstBid===null ? styles.SelfInfoGreen : validatedFirstBid===null ? styles.SelfInfoOrange : validatedFirstBid.bidder === props.manager.id ? styles.SelfInfoGreen :  styles.SelfInfoRed}>
            {props.manager.owner==="Erik" &&
            <button onClick={()=>props.start()}>start</button>
            }
            <button onClick={() => props.logOut() }>Sign out</button>
            {/* Auction timer */}
            <div className={styles.TimerSection}>
                {props.nominatedPlayer !== null && 
                <h3 className={styles.Mini}>Time left:</h3>
                }
                <AuctionView  start={props.start} validatedFirstBid={validatedFirstBid} manager={props.manager} playerQueue={filteredQueue} nominatedPlayer={props.nominatedPlayer} turn={props.turn} l√§hetys={props.l√§hetys} />
                {props.nominatedPlayer !== null &&
                <div>
                <h4 className={styles.Mini}>Nominated by</h4>
                <h4 className={styles.MiniUp}>{props.turn.owner}</h4>
                </div>
                }
            </div>

            {/* Manager toolsit */}
            {props.nominatedPlayer !== null &&
            <div >
                <h4 className={styles.Mini}>Current bid:</h4>
                <h3 className={styles.TimerWithMargins}><strong>${validatedFirstBid.currentPrice}</strong></h3> {/*{props.nominatedPlayer.currentPrice!== null ? props.nominatedPlayer.currentPrice : 1} */}
                <div className={styles.LittlePadding}>
                Quick bid <button disabled={bidButtonDisabled(validatedFirstBid, props.manager)!==true} onClick={()=>bidPlusOne(props.manager)}>${currentBidPlusOne}</button>
                
                <form onSubmit={submit}>
                    <div>
                        Bid <input
                            style={{width: "30px"}}
                            
                            onChange={ ({target}) => setCustomBid(target.value)}
                        />
                    
                    <button disabled={bidButtonDisabled(validatedFirstBid, props.manager)!==true } type="submit">bid</button>
                    </div>
                </form>
                </div>
                <h4 className={styles.Mini}>Highest bid:</h4>
                <h4 className={styles.MiniUp}>{highestBidder.owner}</h4>
            </div>
            }
            {/* Player info */}
            {props.nominatedPlayer !== null &&
            <div className={styles.TimerSection}>
                <h2 className={styles.MiniDown}>Player: {validatedFirstBid.player.playerName} ,  {validatedFirstBid.player.nflTeam}</h2>
                <h4 className={styles.NoMarginOrPadding}>Position: {validatedFirstBid.player.position} Injury: {validatedFirstBid.player.injury !== null ? <strong className={styles.Injured}>{validatedFirstBid.player.injury}</strong> : "none"}</h4>
                <h4 className={styles.NoMarginOrPadding}>Avg price: {validatedFirstBid.player.expectedValue}</h4>
                <h4 className={styles.NoMarginOrPadding}>Bye Week: {validatedFirstBid.player.bye}</h4>
                
                
            </div>
            }
            
        </div>
    )
}

export default AuctionComponent;